*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  color: inherit;
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  border: none;
  background-color: transparent;
}

img, picture, svg, video {
  display: block;
  height: auto;
  vertical-align: middle;
  font-style: italic;
  background-repeat: no-repeat;
  background-size: cover;
}

.content-grid {
  --breakout-size: calc((var(--template-breakout-max-width) - var(--template-max-width)) / 2);
  display: grid;
  grid-template-columns: [full-width-start] minmax(var(--template-padding), 1fr) [breakout-start] minmax(0, var(--breakout-size)) [content-start] min(100% - var(--template-padding) * 2, var(--template-max-width)) [content-end] minmax(0, var(--breakout-size)) [breakout-end] minmax(var(--template-padding), 1fr) [full-width-end];
}

.content-grid > :not(.breakout, .full-width),
.full-width > :not(.breakout, .full-width) {
  grid-column: content;
}

.content-grid > .breakout {
  grid-column: breakout;
}

.content-grid > .full-width {
  grid-column: full-width;
  display: grid;
  grid-template-columns: inherit;
}

:root {
  --template-padding: 0px;
  --section-padding: 0;
  --template-max-width: 1400px;
  --template-breakout-max-width: var(--template-max-width);
  --bg-blue: #D1EBEE;
  --bg-red: #F7E4E0;
  --bg-yellow: #F5F1E3;
  --border-radius: 24px;
  --text-link: rgb(27, 154, 170);
  --text-link-hover: rgb(73, 174, 187);
  --title-gap: 56px;
  --gap: 40px;
  --section-gap: 112px;
}

.text-header-1 {
  line-height: 1;
  font-size: 3.8125rem;
  font-weight: 700;
}

.text-header-2 {
  line-height: 1;
  font-size: 3.0625rem;
  font-weight: 700;
}

.text-header-3 {
  line-height: 1;
  font-size: 2.4375rem;
  font-weight: 700;
}

.text-header-4 {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
}

.text-header-5 {
  line-height: 1;
  font-size: 1.5625rem;
  font-weight: 600;
}

.text-header-6 {
  line-height: 1;
  font-size: 1.25rem;
  font-weight: 600;
}

.text-subtitle-1 {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
}

.text-body-1 {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
}

.text-body-2 {
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
}

.text-center {
  text-align: center;
}

.bg-blue {
  background-color: var(--bg-blue);
}

.bg-red {
  background-color: var(--bg-red);
}

.bg-yellow {
  background-color: var(--bg-yellow);
}

.mb-8 {
  margin-bottom: 8px;
}

.mb-16 {
  margin-bottom: 16px;
}

.mb-32 {
  margin-bottom: 32px;
}

.mt-8 {
  margin-top: 8px;
}

.mt-16 {
  margin-top: 16px;
}

.mt-32 {
  margin-top: 32px;
}

a {
  color: var(--text-link);
}
a:hover {
  color: var(--text-link-hover);
}

body {
  font-family: "Golos Text";
  font-optical-sizing: auto;
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
}
body.no-scroll {
  overflow: hidden;
}

h1 {
  font-weight: 700;
  font-size: 4.75rem;
  line-height: 1;
}

ul {
  padding-left: 24px;
}

.icon-svg {
  width: 24px;
  height: 24px;
}

.warning {
  position: relative;
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  font-weight: 500;
  padding: 4px 12px 4px 34px;
  background: var(--bg-red);
  color: rgb(213, 122, 102);
  border-radius: 16px;
}
.warning::before {
  position: absolute;
  left: 4px;
  content: "!";
  display: block;
  color: white;
  border-radius: 50%;
  background: rgb(213, 122, 102);
  aspect-ratio: 1/1;
  width: 20px;
  height: 20px;
  text-align: center;
}

.social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
}
.social-link--vk .icon-svg {
  width: 20px;
  height: 13px;
}
.social-link--tg .icon-svg {
  width: 20px;
  height: 18px;
}
.social-link--whatsup .icon-svg {
  width: 20px;
  height: 20px;
}
.social-link--ig .icon-svg {
  width: 20px;
  height: 20px;
}

.select-group {
  border: 2px solid rgb(209, 235, 238);
  border-radius: 16px;
  background-color: #fff;
  padding: 8px;
  display: flex;
  flex-direction: row;
  scroll-padding: 8px;
}
.select-group__item {
  background-color: inherit;
  border-radius: 12px;
  padding: 8px;
  text-align: center;
  font-size: 1.125rem;
  font-weight: 500;
  border: none;
  flex-grow: 1;
}
.select-group__item.is-active {
  color: #fff;
  background: rgb(213, 122, 102);
}

.label {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
  border-radius: 100px;
  padding: 3px 6px;
  color: #fff;
  background: rgb(213, 122, 102);
}

.tag {
  padding: 8px 16px;
  font-size: 1.125rem;
  font-weight: 500;
  border: 1.5px solid rgba(27, 154, 170, 0.2);
  border-radius: 100px;
  color: #000;
  background-color: #fff;
  cursor: pointer;
}
.tag.is-active {
  background: rgb(27, 154, 170);
  color: #fff;
}
.tag--disabled, .tag:disabled {
  cursor: default;
  color: rgba(27, 154, 170, 0.2);
}

.button-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1.5px solid rgb(224, 224, 224);
  color: black;
  background-color: transparent;
}
.button-arrow .icon-svg {
  width: 24px;
  height: 24px;
}
.button-arrow--link {
  margin-left: 12px;
  display: inline-flex;
  width: 26.67px;
  height: 26.67px;
  border-color: currentColor;
  color: currentColor;
}
.button-arrow--link .icon-svg {
  width: 12px;
  height: 12px;
}
.button-arrow--left .icon-svg {
  transform: rotate(180deg);
}
.button-arrow--down .icon-svg {
  transform: rotate(90deg);
}
.button-arrow--up .icon-svg {
  transform: rotate(-90deg);
}
.button-arrow:not(.swiper-button-disabled):hover {
  color: var(--text-link-hover);
  border-color: var(--text-link-hover);
}
.button-arrow--filled {
  color: #fff;
  background-color: var(--text-link);
  border-color: var(--text-link);
}
.button-arrow--filled:hover {
  color: #fff;
  background-color: var(--text-link-hover);
  border-color: var(--text-link-hover);
}
.button-arrow.swiper-button-disabled {
  opacity: 0.4;
  cursor: default;
}

.button-audio {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--text-link);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1.5px solid var(--text-link);
  background-color: transparent;
}
.button-audio:hover {
  color: var(--text-link-hover);
  border-color: var(--text-link-hover);
}
.button-audio .icon-svg {
  width: 12px;
  height: 12px;
}
.button-audio--pause .icon-svg {
  width: 16px;
  height: 16px;
}

.button {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 16px;
  gap: 10px;
  background: linear-gradient(180deg, #49AEBB 0%, #1B9AAA 100%);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  color: white;
  font-size: 1rem;
  line-height: 1rem;
  letter-spacing: 0.2px;
  font-weight: 600;
}
.button:hover:not(:disabled) {
  color: white;
  background: rgb(73, 174, 187);
}
.button--block {
  width: 100%;
}
.button--nowrap {
  white-space: nowrap;
}
.button--lg {
  padding: 24px 32px;
  font-size: 1.5625rem;
  line-height: 2rem;
  letter-spacing: 0.5px;
}
.button--md {
  padding: 16px 28px;
  font-size: 1.25rem;
  line-height: 1.5rem;
}
.button--outlined {
  box-shadow: none;
  background: transparent !important;
  color: rgb(27, 154, 170);
  border: 2px solid rgb(27, 154, 170);
}
.button--outlined:hover {
  color: rgb(73, 174, 187);
  border: 2px solid rgb(73, 174, 187);
}
.button:disabled {
  opacity: 0.4;
  cursor: default;
}

.radio {
  display: block;
  position: relative;
  padding-left: 32px;
  user-select: none;
  cursor: pointer;
}
.radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.radio input:checked ~ .radio__fake {
  border-color: rgb(27, 154, 170);
}
.radio input:checked ~ .radio__fake::after {
  display: block !important;
}
.radio__fake {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  height: 24px;
  width: 24px;
  background-color: #fff;
  border: 2px solid rgb(209, 235, 238);
  border-radius: 50%;
}
.radio__fake::after {
  content: "";
  position: absolute;
  display: none;
  left: 50%;
  top: 50%;
  width: 12px;
  height: 12px;
  transform: translate(-50%, -50%);
  background: rgb(27, 154, 170);
  border-radius: 50%;
}
.radio:hover .radio__fake {
  border-color: rgb(27, 154, 170);
}

.input {
  border-radius: 16px;
  background: rgb(255, 255, 255);
  color: black;
  font-size: 1.125rem;
  line-height: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 15px 16px;
  border: 1px solid white;
}
.input::placeholder {
  color: rgb(130, 130, 130);
}
.input.is-error {
  border-color: rgb(213, 122, 102);
}
.input.is-error::placeholder {
  color: rgb(213, 122, 102);
}
.input--block {
  display: block;
  width: 100%;
}

.audio {
  position: relative;
}
.audio audio {
  position: absolute;
  opacity: 0;
  width: 0px;
  height: 0px;
}
.audio__play, .audio__pause {
  display: none;
  gap: 8px;
  grid-auto-flow: column;
  justify-content: start;
  align-items: center;
}
.audio__play.is-active, .audio__pause.is-active {
  display: grid;
}
.audio__progress {
  /* body-2 */
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  color: rgb(130, 130, 130);
}
.audio__progress .icon-svg {
  width: 20px;
  height: 17px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 64px;
  z-index: 2;
  padding: 8px var(--section-padding);
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(16px);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav {
  font-size: 1rem;
  font-weight: 500;
  display: grid;
  gap: 32px;
  grid-auto-flow: column;
}
.header__nav a {
  color: #000;
}
.header__nav a:hover {
  opacity: 0.6;
}
.header__user {
  display: flex;
  align-items: center;
}
.header__user__profile {
  width: 32px;
  height: 32px;
  margin-right: 16px;
  color: #1B9AAA;
}
.header__user__profile .icon-svg {
  width: 32px;
  height: 32px;
}
.header__user__profile:hover {
  color: rgb(73, 174, 187);
}
.header__burger {
  display: none;
  margin-left: 24px;
  width: 24px;
  height: 24px;
  color: #1B9AAA;
}

.section {
  margin-top: var(--section-gap);
  padding-left: var(--section-padding);
  padding-right: var(--section-padding);
}
.section__wrapper {
  z-index: 1;
}
.section__title {
  line-height: 1;
  font-size: 3.0625rem;
  font-weight: 700;
  margin-bottom: var(--title-gap);
}
.section--nopadding {
  padding-left: 0;
  padding-right: 0;
}
.section--nopadding .section__title {
  padding-left: var(--section-padding);
  padding-right: var(--section-padding);
}
.section--main {
  margin-top: 0;
}
.section--main-inner {
  margin-top: 0;
}
.section--prices {
  margin-top: var(--section-gap);
  padding-top: var(--section-gap);
  padding-bottom: 69px;
}
.section--start {
  margin-top: var(--section-gap);
  padding-top: var(--section-gap);
  padding-bottom: var(--section-gap);
}
.section:last-child {
  margin-bottom: var(--section-gap);
}

.breadcrumbs {
  font-size: 0.875rem;
}
.breadcrumbs__link-fake {
  opacity: 0.5;
}
.breadcrumbs__link {
  position: relative;
  color: #000;
  opacity: 0.8;
  margin-right: 24px;
}
.breadcrumbs__link::after {
  position: absolute;
  content: "";
  display: block;
  right: -24px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml;base64,");
  opacity: 0.3;
}

.main-inner {
  padding-top: 88px;
  padding-bottom: 78px;
  min-height: 100vh;
  min-height: 100lvh;
}
.main-inner .section__wrapper {
  margin-top: var(--section-gap);
  display: flex;
  gap: var(--gap);
}
.main-inner__left {
  flex: 1 0 0;
  padding-top: 31px;
}
.main-inner__right {
  flex: 1 0 0;
}
.main-inner__title {
  line-height: 1;
  font-size: 3.8125rem;
  font-weight: 700;
  margin-bottom: 32px;
}
.main-inner__description {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
  margin-bottom: 40px;
}
.main-inner .button {
  position: relative;
}
.main-inner .button::after {
  display: block;
  content: "";
  position: absolute;
  left: -60px;
  bottom: 0;
  transform: translate(-100%, 0) scaleX(-1);
  width: 136px;
  height: 121px;
  background-image: url(../images/decor-arrow-1.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.main-inner__image-container {
  position: relative;
  border-radius: 48px;
}
.main-inner__image-container::before {
  position: absolute;
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: inherit;
  transform: rotate(-10deg);
  opacity: 0.8;
}
.main-inner__image {
  position: relative;
  z-index: 1;
  border-radius: 48px;
  width: 100%;
}

.main {
  position: relative;
  color: #fff;
  font-size: 1.5625rem;
  line-height: 1.25;
  padding-top: 160px;
  padding-bottom: 78px;
}
.main .section__wrapper {
  max-width: 680px;
  padding-left: var(--section-padding);
  padding-right: var(--section-padding);
}
.main__bg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.main__bg {
  grid-column: full-width !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 1s opacity;
  background-image: var(--bg);
}
@media (max-width: 1279px) {
  .main__bg {
    background-image: var(--bg-tablet, var(--bg));
  }
}
@media (max-width: 767px) {
  .main__bg {
    background-image: var(--bg-phone, var(--bg));
  }
}
.main__bg.is-active {
  opacity: 1;
}
.main__title {
  margin-bottom: 32px;
}
.main__signup-form {
  margin-top: 64px;
  position: relative;
}
.main__signup-form::after {
  display: block;
  content: "";
  position: absolute;
  right: -80px;
  top: 50%;
  transform: translate(100%, -50%);
  width: 136px;
  height: 121px;
  background-image: url(../images/decor-arrow-1.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.form .warning {
  margin-top: 8px;
}
.form__row {
  margin-bottom: 8px;
  display: flex;
  align-items: center;
}
.form__row--last {
  margin-top: 16px !important;
  margin-bottom: 0 !important;
}
.form__row-inline {
  margin-bottom: 8px;
  display: grid;
  gap: 8px;
  grid-auto-flow: column;
  align-items: center;
}

.signup-form {
  border-radius: var(--border-radius);
  color: black;
  padding: 40px;
}
.signup-form--popup {
  padding: 0 !important;
}
.signup-form p {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
}
.signup-form__title {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  margin-bottom: 32px;
  max-width: 400px;
  display: flex;
}
.signup-form__warn {
  margin-left: 12px;
  font-size: 0.8125rem;
  line-height: 1.25;
  color: rgb(130, 130, 130);
}

.speak__title {
  text-align: center;
  line-height: 1;
  font-size: 3.0625rem;
  font-weight: 700;
  margin-bottom: 24px;
}
.speak__subtitle {
  font-weight: 400;
  font-size: 1.5625rem;
  line-height: 1.25;
  margin-bottom: var(--title-gap);
  text-align: center;
  color: #828282;
}
.speak__cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--gap);
}
.speak__card {
  border-radius: var(--border-radius);
  padding: 64px 40px 40px;
  background-size: contain;
  background-position: right bottom;
  background-repeat: no-repeat;
  height: 428px;
}
.speak__card:nth-child(1) {
  background-image: url(../images/card-bg-1.jpeg);
}
.speak__card:nth-child(2) {
  background-image: url(../images/card-bg-2.jpeg);
}
.speak__card:nth-child(3) {
  grid-column: 1/3;
  display: flex;
  gap: 50px;
  padding-right: 0;
}
.speak__card__title {
  line-height: 1;
  font-size: 1.9375rem;
  font-weight: 700;
  margin-bottom: 24px;
  max-width: 50%;
}
.speak__card__description {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
  opacity: 0.6;
  max-width: 50%;
}
.speak__card__right {
  overflow: hidden;
  position: relative;
}
.speak__card__right::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 80px;
  height: 100%;
  background: linear-gradient(270deg, rgb(209, 235, 238), rgba(209, 235, 238, 0) 100%);
  z-index: 1;
}
.speak__card__left {
  flex: 0 0 25%;
}
.speak__card__left > div {
  max-width: 100%;
}
.speak__card__slider {
  display: flex;
  gap: 20px;
  overflow: hidden;
}
.speak__card__slider img {
  width: 533px;
  height: 300px;
}
.speak__card__slider-pagination {
  display: flex;
  gap: 16px;
  justify-content: center;
  margin-top: 20px;
}
.speak__card__slider-page {
  position: relative;
  width: 80px;
  height: 4px;
  border-radius: 100px;
  background: rgb(27, 154, 170);
  opacity: 0.2;
}
.speak__card__slider-page.is-active {
  background-color: #fff;
  opacity: 1;
}
.speak__card__slider-page.is-active::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  border-radius: inherit;
  background: rgb(27, 154, 170);
  width: var(--progress);
}

.test .section__wrapper {
  display: flex;
  flex-direction: row;
  gap: var(--gap);
  align-items: center;
}
.test__image {
  width: 560px;
}
.test__right {
  display: flex;
  flex-direction: column;
  gap: 32px;
  align-items: flex-start;
}
.test__title {
  line-height: 1;
  font-size: 3.8125rem;
  font-weight: 700;
}
.test__description {
  /* body-1 */
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.25;
}

.courses__items {
  display: grid;
  grid-auto-flow: row;
  gap: 40px;
  justify-items: center;
}

.courses-item {
  display: grid;
  gap: var(--gap);
